@charset "UTF-8";
:root {
  --innerValue: 1100;
  --inner: 1100px;
  --padding-pc: 25px;
  --padding-sp: 20px;
  --light: 300;
  --normal: 400;
  --medium: 500;
  --semi-bold: 600;
  --bold: 700;
  --ex-bold: 800;
  --base-font-family: "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  --base-color: #666;
  --base-background: #fff;
  --back: #333;
  --black: #333;
  --white: #fff;
  --btn-fucus: #fff;
  --header: 100;
  --hamburger: 103;
  --drawer: 102;
  --drawer-back: 101;
  --page-top: 99;
  --base-line-height: 1.5;
}

/********************************
* セッティング反映
********************************/
body {
  color: var(--base-color);
  font-family: var(--base-font-family);
  font-weight: var(--normal);
  line-height: var(--base-line-height);
  background: var(--base-background);
}

a:focus-visible,
button:focus-visible {
  outline: 2px solid #007bff;
}

.u-desktop {
  display: block;
}
@media screen and (max-width: 767px) {
  .u-desktop {
    display: none;
  }
}

.u-mobile {
  display: none;
}
@media screen and (max-width: 767px) {
  .u-mobile {
    display: block;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 1100px) {
  html {
    font-size: clamp(10px, 16 / var(--innerValue) * 100vw, 16px);
  }
}
@media screen and (max-width: 767px) {
  html {
    font-size: 16px;
  }
}
@media (max-width: 375px) {
  html {
    font-size: clamp(10px, 16 / 375 * 100vw, 16px);
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

li {
  list-style: none;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  display: flex;
  flex-direction: column;
  overflow-wrap: anywhere;
  word-break: normal;
  line-break: strict;
}

main {
  flex: 1;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  display: block;
  max-width: 100%;
  height: auto;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

textarea {
  resize: vertical;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

:where(button, [type=button], [type=reset], [type=submit]) {
  touch-action: manipulation;
}

/* Blur images when they have no alt attribute */
/* フォームリセット */
.c-btn {
  inline-size: fit-content;
  outline-offset: 2px;
  touch-action: manipulation;
  user-select: none;
}
.c-btn:focus-visible {
  outline-offset: 2px;
}

.c-hamburger {
  --hamburger-color1: var(--white);
  --hamburger-color2: var(--white);
}
@media screen and (max-width: 767px) {
  .c-hamburger {
    display: inline-block;
    text-decoration: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border: none;
    background: var(--black);
    cursor: pointer;
    -webkit-transition: all 0.3s ease-in-out;
    transition: background 0.3s ease;
    position: relative;
    width: calc(50 / 16 * 1rem);
    height: calc(50 / 16 * 1rem);
    border-radius: 50%;
  }
}

@media (hover: hover) {
  .c-hamburger:hover {
    background: var(--black);
  }
  .c-hamburger:hover .c-hamburger__line {
    background: var(--hamburger-color2);
  }
  .c-hamburger:hover[aria-expanded=true] .c-hamburger__line {
    background: transparent;
  }
}
.c-hamburger__line {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: calc(20 / 16 * 1rem);
  height: 1px;
  background: var(--hamburger-color1);
  transition: background 0.3s ease;
}

.c-hamburger__line::before,
.c-hamburger__line::after {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background: inherit;
  content: "";
  transition: transform 0.3s ease;
}

.c-hamburger__line::before {
  top: calc(-6 / 16 * 1rem);
}

.c-hamburger__line::after {
  top: calc(6 / 16 * 1rem);
}

.c-hamburger[aria-expanded=true] {
  background: var(--black);
}
.c-hamburger[aria-expanded=true] .c-hamburger__line {
  background: transparent;
}
.c-hamburger[aria-expanded=true] .c-hamburger__line::before, .c-hamburger[aria-expanded=true] .c-hamburger__line::after {
  top: 0;
  background: var(--hamburger-color2);
}
.c-hamburger[aria-expanded=true] .c-hamburger__line::before {
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
.c-hamburger[aria-expanded=true] .c-hamburger__line::after {
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.c-page-top {
  width: calc(45 / 16 * 1rem);
  height: calc(45 / 16 * 1rem);
  background: var(--white);
  position: fixed;
  bottom: calc(50 / 16 * 1rem);
  right: calc(20 / 16 * 1rem);
  transition: 0.3s;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  z-index: var(--page-top);
}

.c-page-top.is-active {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}

.c-page-top span:first-child {
  display: inline-block;
  width: calc(20 / 16 * 1rem);
  height: calc(2 / 16 * 1rem);
  background: var(--back);
  transform: rotate(-45deg) translateX(3px);
  transform-origin: right;
  transition: 0.3s;
}

.c-page-top span:nth-child(2) {
  display: inline-block;
  width: calc(20 / 16 * 1rem);
  height: calc(2 / 16 * 1rem);
  background: var(--back);
  transform: rotate(45deg) translateX(-3px);
  transform-origin: left;
  transition: 0.3s;
}

.c-page-top:hover {
  background: var(--back);
}
.c-page-top:hover span {
  background: var(--white);
}

.pagination {
  display: inline-block;
}
.pagination .nav-links {
  display: flex;
  gap: calc(15 / 16 * 1rem);
}
@media screen and (max-width: 767px) {
  .pagination .nav-links {
    gap: calc(10 / 16 * 1rem);
  }
}
.pagination .page-numbers {
  display: grid;
  place-content: center;
  padding: calc(10 / 16 * 1rem);
  width: calc(80 / 16 * 1rem);
  aspect-ratio: 1;
  border-radius: 50%;
  color: var(--base-color);
  background: #fff;
  border: 1px solid #ccc;
  font-size: calc(16 / 16 * 1rem);
  font-weight: var(--bold);
  transition: all 0.3s ease;
}
@media screen and (max-width: 767px) {
  .pagination .page-numbers {
    width: calc(35 / 16 * 1rem);
    padding: calc(5 / 16 * 1rem);
    font-size: calc(12 / 16 * 1rem);
  }
}
@media (hover: hover) {
  .pagination .page-numbers:hover {
    background: var(--base-color);
    color: #fff;
  }
}
.pagination .current {
  background: var(--base-color);
  color: #fff;
}
.pagination .prev,
.pagination .next {
  position: relative;
}
.pagination .prev::after,
.pagination .next::after {
  content: "";
  display: block;
  width: calc(22 / 16 * 1rem);
  height: calc(16 / 16 * 1rem);
  position: absolute;
  inset: 0;
  margin: auto;
}
@media screen and (max-width: 767px) {
  .pagination .prev::after,
  .pagination .next::after {
    width: calc(15 / 16 * 1rem);
    height: calc(11 / 16 * 1rem);
  }
}
.pagination .prev {
  transform: rotate(180deg);
}
.pagination .dots {
  background: transparent;
  box-shadow: none;
}

.l-inner {
  width: 100%;
  max-width: calc(var(--inner) + var(--padding-pc) * 2);
  padding-inline: var(--padding-pc);
  margin-inline: auto;
}
@media screen and (max-width: 767px) {
  .l-inner {
    padding-inline: var(--padding-sp);
  }
}

.l-parts {
  width: 100%;
  height: 100%;
  background-color: #9cdbe8;
  padding: calc(100 / 16 * 1rem) 0;
}

.l-parts__sec {
  padding: calc(50 / 16 * 1rem) 0;
}

.p-drawer-menu {
  display: none;
}
@media screen and (max-width: 767px) {
  .p-drawer-menu {
    display: block;
    position: fixed;
    top: calc(80 / 16 * 1rem);
    bottom: 0;
    right: 0;
    left: 0;
    width: 100%;
    height: calc(100% - (80 / 16 * 1rem));
    overflow-y: scroll;
    z-index: var(--drawer);
    background: #ccc;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: opacity 0.3s ease, visibility 0.3s ease, background 0.3s ease;
  }
}

.p-drawer-menu[aria-hidden=false] {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.p-drawer-menu__items {
  height: initial;
  display: block;
  padding-block: calc(135 / 16 * 1rem) calc(100 / 16 * 1rem);
  padding-inline: var(--padding-pc);
  width: 100%;
  margin-inline: auto;
}
@media screen and (max-width: 767px) {
  .p-drawer-menu__items {
    padding-block: calc(50 / 16 * 1rem) calc(100 / 16 * 1rem);
    padding-inline: var(--padding-sp);
  }
}

.p-drawer-menu__item {
  border-bottom: 1px solid var(--white);
}
.p-drawer-menu__item a {
  display: block;
  width: 100%;
  height: 100%;
  padding: calc(10 / 16 * 1rem);
  min-height: calc(80 / 16 * 1rem);
  text-align: center;
  font-size: calc(28 / 16 * 1rem);
  line-height: 1.4285714286;
  transition: opacity 0.3s ease;
  position: relative;
  display: flex;
  align-items: center;
  gap: calc(20 / 16 * 1rem);
  text-align: left;
}
@media screen and (max-width: 767px) {
  .p-drawer-menu__item a {
    font-size: calc(18 / 16 * 1rem);
    line-height: 1.4444444444;
    min-height: calc(60 / 16 * 1rem);
  }
}
.p-drawer-menu__item a::before {
  content: "";
  display: block;
  width: calc(20 / 16 * 1rem);
  flex: 0 0 calc(20 / 16 * 1rem);
  height: calc(29 / 16 * 1rem);
  background: var(--black);
  clip-path: polygon(0 0, 0% 100%, 100% 50%);
}
@media screen and (max-width: 767px) {
  .p-drawer-menu__item a::before {
    width: calc(12 / 16 * 1rem);
    flex: 0 0 calc(12 / 16 * 1rem);
    height: calc(17 / 16 * 1rem);
  }
}
@media (hover: hover) {
  .p-drawer-menu__item a:hover {
    opacity: 0.7;
  }
}

.p-drawer-menu__bottom a {
  display: block;
  min-height: calc(80 / 16 * 1rem);
  font-size: calc(22 / 16 * 1rem);
  color: var(--white);
  display: flex;
  align-items: center;
  text-align: left;
  padding: calc(10 / 16 * 1rem) 0;
  transition: opacity 0.3s ease;
}
@media screen and (max-width: 767px) {
  .p-drawer-menu__bottom a {
    min-height: calc(60 / 16 * 1rem);
    font-size: calc(14 / 16 * 1rem);
  }
}
@media (hover: hover) {
  .p-drawer-menu__bottom a:hover {
    opacity: 0.7;
  }
}

.p-footer {
  padding: calc(32 / 16 * 1rem) calc(32 / 16 * 1rem) calc(60 / 16 * 1rem);
}
@media screen and (max-width: 767px) {
  .p-footer {
    padding: calc(58 / 16 * 1rem) 0 calc(32 / 16 * 1rem);
  }
}

.p-footer__inner {
  display: flex;
  align-items: center;
}
@media screen and (max-width: 767px) {
  .p-footer__inner {
    display: block;
  }
}

.p-footer__logo {
  width: calc(70 / 16 * 1rem);
}
@media screen and (max-width: 767px) {
  .p-footer__logo {
    width: calc(70 / 16 * 1rem);
    margin: auto;
  }
  .p-footer__logo img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}

.p-footer__body {
  margin-left: auto;
}
@media screen and (max-width: 767px) {
  .p-footer__body {
    margin-top: calc(8 / 16 * 1rem);
  }
}

.p-footer__items {
  display: flex;
  align-items: center;
}
@media screen and (max-width: 767px) {
  .p-footer__items {
    display: block;
  }
}

.p-footer__item {
  font-size: calc(16 / 16 * 1rem);
  position: relative;
  color: var(--white);
  text-align: center;
}

.p-footer__item::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  margin-top: calc(5 / 16 * 1rem);
  background: var(--back);
  opacity: 0;
  transition: 0.3s;
}

.p-footer__item:hover::after {
  opacity: 1;
}

.p-footer__item + .p-footer__item {
  margin-top: initial;
  margin-left: calc(32 / 16 * 1rem);
}
@media screen and (max-width: 767px) {
  .p-footer__item + .p-footer__item {
    margin-top: calc(8 / 16 * 1rem);
    margin-left: initial;
  }
}

.p-footer__copy {
  font-size: calc(11 / 16 * 1rem);
  color: var(--white);
  text-align: center;
  padding-bottom: calc(60 / 16 * 1rem);
}
@media screen and (max-width: 767px) {
  .p-footer__copy {
    font-size: calc(11 / 16 * 1rem);
  }
}

.p-header-menu {
  display: flex;
  align-items: center;
}
@media screen and (max-width: 767px) {
  .p-header-menu {
    display: none;
  }
}

.p-header-menu__items {
  display: flex;
  align-items: center;
  gap: calc(50 / 16 * 1rem);
  height: 100%;
}

.p-header-menu__item {
  font-size: calc(12 / 16 * 1rem);
  height: 100%;
  display: flex;
  align-items: center;
  position: relative;
}
.p-header-menu__item::after {
  content: "";
  display: block;
  width: calc(8 / 16 * 1rem);
  aspect-ratio: 1;
  border-radius: 50%;
  background: var(--base-color);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  transition: all 0.5s ease;
  opacity: 0;
}
.p-header-menu__item a {
  display: flex;
  align-items: center;
  height: 100%;
  gap: calc(9 / 16 * 1rem);
}
@media (hover: hover) {
  .p-header-menu__item:hover::after {
    opacity: 1;
    transform: translate(-50%, -300%);
  }
}

.p-header-menu__btn {
  font-size: calc(16 / 16 * 1rem);
  height: 100%;
  display: flex;
  align-items: center;
}

.p-header-menu__item--subMenu {
  cursor: pointer;
}
@media (hover: hover) {
  .p-header-menu__item--subMenu:hover .p-header-menu__subItems {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }
}

.p-header-menu__subItems {
  position: absolute;
  bottom: 0;
  transform: translateY(100%);
  width: fit-content;
  padding: calc(30 / 16 * 1rem) calc(40 / 16 * 1rem);
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: calc(20 / 16 * 1rem);
  background: var(--white);
  border-radius: 30px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: all 0.3s ease;
}

.p-header-menu__subItem {
  font-size: calc(13 / 16 * 1rem);
  padding-left: calc(16 / 16 * 1rem);
  position: relative;
  width: calc(170 / 16 * 1rem);
  transition: padding-left 0.3s ease;
}
.p-header-menu__subItem::before {
  content: "";
  display: block;
  width: calc(8 / 16 * 1rem);
  aspect-ratio: 1;
  border-radius: 50%;
  background: rgba(204, 204, 204, 0.7);
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
  z-index: 2;
  transition: background 0.3s ease;
}
@media (hover: hover) {
  .p-header-menu__subItem:hover {
    padding-left: calc(20 / 16 * 1rem);
  }
  .p-header-menu__subItem:hover::before {
    background: var(--base-color);
  }
}

.p-header {
  height: calc(100 / 16 * 1rem);
  background: var(--white);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: var(--header);
  display: flex;
  transition: background 0.3s ease;
}
@media screen and (max-width: 767px) {
  .p-header {
    height: calc(80 / 16 * 1rem);
  }
}
@media print {
  .p-header {
    position: absolute;
  }
}
.p-header.is_active {
  background: #ccc;
}

.p-header__inner {
  display: flex;
  gap: calc(20 / 16 * 1rem);
  justify-content: space-between;
  width: 100%;
  padding-inline: calc(50 / 16 * 1rem) calc(50 / 16 * 1rem);
  height: auto;
}
@media screen and (max-width: 767px) {
  .p-header__inner {
    padding-inline: calc(30 / 16 * 1rem);
  }
}

.p-header__logo {
  width: calc(150 / 16 * 1rem);
  height: initial;
  display: flex;
  align-items: center;
}
.p-header__logo a {
  width: 100%;
  display: block;
}
@media screen and (max-width: 767px) {
  .p-header__logo {
    width: calc(120 / 16 * 1rem);
  }
}

.p-header__logo img {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
}

.p-header__drawerBtn {
  display: none;
}
@media screen and (max-width: 767px) {
  .p-header__drawerBtn {
    display: grid;
    place-content: center;
    z-index: var(--hamburger);
    position: relative;
  }
}

/* 背景固定用CSS */
@media screen and (max-width: 767px) {
  body:has(.c-hamburger.is_active) {
    overflow: hidden;
  }
}

.u-hidden-pc {
  display: none;
}
@media screen and (max-width: 767px) {
  .u-hidden-pc {
    display: block;
  }
}

.u-hidden-sp {
  display: block;
}
@media screen and (max-width: 767px) {
  .u-hidden-sp {
    display: none;
  }
}