@use "global" as *;

.p-footer {
  padding: rem(32) rem(32) rem(60);

  @include mq(md) {
    padding: rem(58) 0 rem(32);
  }
}
.p-footer__inner {
  display: flex;
  align-items: center;

  @include mq(md) {
    display: block;
  }
}
.p-footer__logo {
  width: rem(70);

  @include mq(md) {
    width: rem(70);
    margin: auto;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
}
.p-footer__body {
  margin-left: auto;

  @include mq(md) {
    margin-top: rem(8);
  }
}
.p-footer__items {
  display: flex;
  align-items: center;

  @include mq(md) {
    display: block;
  }
}
.p-footer__item {
  font-size: rem(16);
  position: relative;
  color: var(--white);
  text-align: center;
}
.p-footer__item::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  margin-top: rem(5);
  background: var(--back);
  opacity: 0;
  transition: 0.3s;
}
.p-footer__item:hover::after {
  opacity: 1;
}
.p-footer__item + .p-footer__item {
  margin-top: initial;
  margin-left: rem(32);

  @include mq(md) {
    margin-top: rem(8);
    margin-left: initial;
  }
}
.p-footer__copy {
  font-size: rem(11);
  color: var(--white);
  text-align: center;
  padding-bottom: rem(60);

  @include mq(md) {
    font-size: rem(11);
  }
}
